#mobile_nav_toggle {
  position: fixed;
  right: 0;
  top: 0;
  width: 60px;
  height: 60px;
  padding: 18px 16px;
  background: #000000;
  display: none;
  border: none;
  outline: none;
  cursor: pointer;
  z-index: 999;
  margin: 0;
  transition: all 0.4s;
  -webkit-transition: all 0.4s;
  -moz-transition: all 0.4s;
  -ms-transition: all 0.4s;
  -o-transition: all 0.4s;
}

#mobile_nav_toggle .drawer_icon {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  margin: 0;
}

#mobile_nav_toggle .drawer_icon span {
  display: inline-block;
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background: #FFFFFF;
  transition: all 0.4s;
  -webkit-transition: all 0.4s;
  -moz-transition: all 0.4s;
  -ms-transition: all 0.4s;
  -o-transition: all 0.4s;
  transform-origin: center center;
}

#mobile_nav_toggle .drawer_icon span:nth-child(1) {
  top: 0;
}

#mobile_nav_toggle .drawer_icon span:nth-child(2) {
  top: calc(50% - 1px);
}

#mobile_nav_toggle .drawer_icon span:nth-child(3) {
  bottom: 0;
}

#mobile_nav_toggle.drawer_open .drawer_icon {
  position: relative;
}

#mobile_nav_toggle.drawer_open .drawer_icon span:nth-child(1) {
  top: calc(50% - 2px);
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -o-transform: rotate(45deg);
}

#mobile_nav_toggle.drawer_open .drawer_icon span:nth-child(2) {
  opacity: 0;
  visibility: hidden;
}

#mobile_nav_toggle.drawer_open .drawer_icon span:nth-child(3) {
  top: calc(50% - 2px);
  bottom: auto;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
